import { HttpErrorResponse } from '@angular/common/http';
import { Component, Directive, EventEmitter, Input, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NotificationService } from '@backbase/ui-ang/notification';
import { ManageProfilePreferencesService, UserDataService } from '@backbase/user-common';
import { first } from 'rxjs/operators';
import {
  UserIdentitySecurityCenterActionType,
  UserIdentitySecurityCenterWidgetStatus,
} from './types/user-identity-security-center-widget.type';

@Component({
  selector: 'bb-user-identity-security-center-widget',
  templateUrl: './user-identity-security-center-widget.component.html',
  providers: [ManageProfilePreferencesService, UserDataService],
})
export class UserIdentitySecurityCenterWidgetComponent implements OnInit {
  @ViewChild('notificationSuccess', { static: true }) notificationSuccessTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationFailure', { static: true }) notificationFailureTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationFailureLogin') notificationFailureTemplateLogin: TemplateRef<any> | undefined;

  readonly hostRef = this;
  openPassword = false;
  private notificationDelay: number | undefined;

  @Output() readonly widgetStatus = new EventEmitter<UserIdentitySecurityCenterWidgetStatus>();

  /**
   * Time to live for the success notification message in seconds
   */
  @Input() notificationDismissTime: number | undefined;

  constructor(
    private readonly notificationService: NotificationService,
    private readonly manageProfilePreferencesService: ManageProfilePreferencesService,
  ) {}

  ngOnInit() {
    if (this.notificationDismissTime) {
      this.notificationDelay = this.notificationDismissTime * 1000;
    } else {
      this.manageProfilePreferencesService
        .getNotificationDismissTimePreference()
        .pipe(first())
        .subscribe((time) => (this.notificationDelay = time * 1000));
    }
  }

  toggleChangePassword() {
    this.openPassword = !this.openPassword;
  }

  emitPasswordChanged() {
    this.showNotification(this.notificationSuccessTemplate as TemplateRef<any>, 'success');
    this.widgetStatus.emit({
      type: UserIdentitySecurityCenterActionType.PasswordChanged,
    });
  }

  emitError(error: HttpErrorResponse) {
    if (error.status === 400) {
      this.showNotification(this.notificationFailureTemplateLogin as TemplateRef<any>, 'error');
    } else {
      this.showNotification(this.notificationFailureTemplate as TemplateRef<any>, 'error');
    }
    this.widgetStatus.emit({
      type: UserIdentitySecurityCenterActionType.PasswordChangeFailed,
    });
  }

  private showNotification(template: TemplateRef<any>, type: 'success' | 'error'): void {
    this.notificationService.showNotification({
      message: template,
      modifier: type,
      ttl: this.notificationDelay,
    });
  }
}

@Directive({
  selector: '[bbUserIdentitySecurityCenterWidgetCustomizable]',
})
export class UserIdentitySecurityCenterCustomizableDirective extends BbTemplate<
  UserIdentitySecurityCenterWidgetComponent,
  undefined
> {}
